<template>
  <div class="playingpage" v-if="datas">
    <div class="videowrap">
      <div class="b">
        <van-icon name="arrow-left" @click="hideplayingpage" />
      </div>
      <video v-if="datas.content_video" :src="datas.content_video[0].progressive[2].url" controls></video>
    </div>
    <div class="a" :style="{height:hei}"></div>
    <div class="context">
      <h3>{{datas.title}}</h3>
      <h4 v-if="datas.cate">{{datas.cate[0]}} / {{counttime(datas.duration)}}</h4>
      <p>{{datas.intro}}</p>
      <div class="author" v-if="datas.author">
        <div>
          <img :src="datas.author.profile.avatar" />
          <div>
            <span>{{datas.author.profile.username}}</span>
            <p>
              片中职务：
              <span v-for="(item, index) in datas.author.roles" :key="index">{{item}}</span>
            </p>
          </div>
        </div>
        <van-icon name="arrow-right" />
      </div>
      <!-- <div class="relate">
        {{datas.cate}}
        {{datas.cate}}
      </div>-->

      <h5>{{datas.comment.total}} 条评论</h5>
      <div class="comment" v-for="(item, index) in datas.comment.list" :key="index">
        <div>
          <div>
            <img :src="item.userinfo.avatar" />
            <div>
              <p>{{item.userinfo.username}}</p>
              <a>{{index+1}}小时前</a>
            </div>
          </div>
          <span>
            <van-icon name="good-job-o" />
            {{item.count_approve}}
          </span>
        </div>
        <p>{{item.content}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Playingpage",
  props: ["datas"],
  data() {
    return {
      hei: 0,
      a: 0
    };
  },
  computed: {
    //  showplayingpage() {
    //   return this.$store.state.showplayingpage;
    // },
    // playingpagedatas(){
    //   return this.$store.state.playingpagedatas;
    // }
  },
  methods: {
    counttime(t) {
      return (
        parseInt(t / 60) + "'" + (t % 60 > 9 ? t % 60 : "0" + (t % 60)) + '"'
      );
    },
    hideplayingpage() {
      this.$store.state.showrouter = true;
      this.$store.state.showplayingpage = false;
    }
  },

  watch: {
    a() {
      this.$nextTick(function() {
        let videowrap = document.getElementsByClassName("videowrap");

        // console.log(videowrap[0].offsetHeight);
        this.hei = videowrap[0].offsetHeight + "px";
      });
    }
  },
  created() {
    let timer = setInterval(() => {
      this.a += 1;
    }, 300);
    setTimeout(() => {
      clearInterval(timer);
    }, 2000);
  }
};
</script>

<style lang="less" scoped>
.playingpage {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  font-size: 14px;
  height: 100vh;
  overflow-y: auto;

  .videowrap {
    text-align: left;

    position: fixed;
    overflow: hidden;
    top: 0;
    left: 0;
    z-index: 3;
    video {
      width: 100%;
    }
    .b {
      width: 100%;
      background: #fff;
    }
    .van-icon {
      font-size: 20px;
      font-weight: 800;
      padding: 5px 15px;
      z-index: 9;
    }
  }
  .context {
    & > p {
      padding: 20px 15px;
      text-align: left;
    }
  }
  img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }
  .comment {
    border-bottom: 1px solid rgb(214, 210, 210);
    padding: 15px;
    & > p {
      padding: 10px 0 10px 60px;
      text-align: left;
    }
    div {
      display: flex;
      justify-content: space-between;
      align-items: center;
      div {
        div {
          display: flex;
          flex-direction: column;

          align-items: flex-start;
        }
      }
    }
  }
  h5 {
    text-align: left;
    font-weight: 700;
    padding: 15px;
  }
  .relate {
    height: 150px;

    border-bottom: 1px solid rgb(214, 210, 210);
  }

  h3 {
    font-size: 18px;
    font-weight: 700;
    padding: 15px 0;
    font-size: 16px;
  }

  .author {
    border-top: 1px solid rgb(214, 210, 210);
    border-bottom: 1px solid rgb(214, 210, 210);
    padding: 10px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    div {
      display: flex;
      justify-content: flex-start;
      div {
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        & > span {
          font-weight: 700;
        }
      }
    }
  }
}
</style>



