<template>
  <div class="home" v-show="showrouter">
    <!-- <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(banner, index) in banners.list" :key="index">
        <img :src="banner.image" />
      </van-swipe-item>
     
    </van-swipe>-->
    <template v-if="banners">
      <Homeswipe :banners="banners.list"></Homeswipe>

      <Homelist :lists="todays.list" :hshow="true">今日</Homelist>
      <Homelist :lists="hots.list" :mode="true" :hshow="true">热门</Homelist>

      <Homeswipe :banners="albums.list" :modes="true">专题</Homeswipe>

      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <van-cell v-for="(post, index) in posts" :key="index">
          <Homelist :lists="post.list" :hshow="true">{{ post.selection_title }}</Homelist>
        </van-cell>
      </van-list>
    </template>

    <!-- <Playingpage v-if="showplayingpage" :datas="playingpagedatas"></Playingpage> -->
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import Homelist from "@/components/Homelist.vue";
import Homeswipe from "@/components/Homeswipe.vue";
// import Playingpage from "@/components/Playingpage.vue";

export default {
  name: "Home",
  components: {
    Homelist,
    Homeswipe
    // Playingpage
  },
  data() {
    return {
      loading: false,
      finished: false
    };
  },

  methods: {
    onLoad() {
      // console.log(this.posts);
      let id = this.posts.slice(this.posts.length - 1)[0].lastid;
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/index/getIndexPosts/lastid/" +
            id
        )
        .then(response => {
          // console.log(response.data.data);
          this.posts.push(response.data.data);
          this.loading = false;
        })
        .catch(() => {
          this.finished = true;
        });
    }
  },
  computed: {
    albums() {
      return this.$store.state.homedatas.album;
    },
    banners() {
      return this.$store.state.homedatas.banner;
    },
    hots() {
      return this.$store.state.homedatas.hot;
    },
    posts() {
      return [this.$store.state.homedatas.posts];
    },
    todays() {
      return this.$store.state.homedatas.today;
    },
    showplayingpage() {
      return this.$store.state.showplayingpage;
    },
    showrouter() {
      return this.$store.state.showrouter;
    }

    // playingpagedatas(){
    //   return this.$store.state.playingpagedatas;
    // }
  }
};
</script>

<style lang="less" >
.van-list {
  .van-cell {
    padding: 0;
  }
}
</style>
