<template>
  <div id="app">
    <div id="nav">
      <van-nav-bar :left-text="days" :right-text="times">
        <!-- <template #right>
          <van-icon name="search" size="18" />
        </template>
        <template #left>
          <van-icon name="search" size="18" />
        </template>-->

        <template #title>
          <van-tabs v-model="activeName" @click="gorouter" background="#2c3e50" color="#fff">
            <van-tab title="发现" name="home"></van-tab>
            <van-tab title="频道" name="channel"></van-tab>
            <van-tab title="我的" name="me"></van-tab>
          </van-tabs>
        </template>
      </van-nav-bar>
    </div>
    <router-view />
    <Playingpage v-if="showplayingpage" :datas="playingpagedatas"></Playingpage>
    <div class="loading" v-if="showloading">
      <van-icon name="arrow-left" @click="hideplayingpage" />
      <van-loading size="36px" vertical color="rgb(44, 197, 197)">加载中...</van-loading>
    </div>
  </div>
</template>

<script>
import Playingpage from "@/components/Playingpage.vue";

export default {
  data: function() {
    return {
      activeName: "home",
      days: null,
      times: null
    };
  },
  components: { Playingpage },
  computed: {
    showplayingpage() {
      return this.$store.state.showplayingpage;
    },
    playingpagedatas() {
      return this.$store.state.playingpagedatas;
    },
    showloading() {
      return this.$store.state.showloading;
    }
  },

  created() {
    // this.$router.push("/");

    // time  day
    this.$data.days =
      new Date().getMonth() + 1 + "月" + new Date().getDay() + "日";
    this.$data.times =
      new Date().getHours() +
      ":" +
      (new Date().getMinutes() > 9
        ? new Date().getMinutes()
        : "0" + new Date().getMinutes());
    setInterval(() => {
      this.$data.days =
        new Date().getMonth() + 1 + "月" + new Date().getDay() + "日";
      this.$data.times =
        new Date().getHours() +
        ":" +
        (new Date().getMinutes() > 9
          ? new Date().getMinutes()
          : "0" + new Date().getMinutes());
    }, 60000);

    // gethomedatas
    let storagehomedata = JSON.parse(window.localStorage.getItem("homedata"));
    if (storagehomedata && storagehomedata.endtime > Date.now()) {
      this.$store.state.homedatas = storagehomedata.datas;
    } else {
      this.gethomedatas();
    }
    // getchanneldatas
    let storagechanneldata = JSON.parse(
      window.localStorage.getItem("channeldata")
    );
    if (storagechanneldata && storagechanneldata.endtime > Date.now()) {
      this.$store.state.channeldatas = storagechanneldata.datas;
    } else {
      this.getchanneldatas();
    }
  },
  methods: {
    hideplayingpage() {
      this.$store.state.showloading = false;
      this.$store.state.showrouter = true;
    },
    gorouter() {
      let a = this.$data.activeName;

      if (a == "home") {
        this.$router.push("/");
      } else if (a == "channel") {
        this.$router.push("/channel");
      } else {
        this.$router.push("/me");
      }
    },
    gethomedatas() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/index/index"
        )
        .then(response => {
          // console.log(response.data.data);
          // console.log(this.$store.state.homedatas);
          this.$store.state.homedatas = response.data.data;

          window.localStorage.setItem(
            "homedata",
            JSON.stringify({
              endtime: Date.now() + 1 * 60 * 60 * 1000,
              datas: response.data.data
            })
          );
        });
    },
    getchanneldatas() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/cate/getList"
        )
        .then(response => {
          this.$store.state.channeldatas = response.data.data;
          window.localStorage.setItem(
            "channeldata",
            JSON.stringify({
              endtime: Date.now() + 1 * 60 * 60 * 1000,
              datas: response.data.data
            })
          );
        });
    }
  }
};
</script>

<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
}
.loading {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  background: rgb(215, 223, 223);
  z-index: 1;
  .van-icon {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    padding: 15px;
  }
}
#nav {
  .van-nav-bar {
    background: #2c3e50;
    color: #fff;
    .van-tabs {
      .van-tab {
        padding: 0 10px;
        color: wheat !important;
        &.van-tab--active {
          color: wheat !important;
        }
      }
    }
  }
}
</style>
