import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showrouter:true,
    showloading:false,
    showplayingpage:false,
    showchannellist:false,
    homedatas:[],
    channeldatas:[],
    playingpagedatas:[]
  },
  mutations: {
    changeplayingpagedatas(state,value){
      state.playingpagedatas = value
    }
  },
  actions: {},
  modules: {}
});
