<template>
  <div class="homeswipe">
    <h3 v-if="modes">
      <slot></slot>
    </h3>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item
        :class="{modes:modes}"
        v-for="(banner, index) in banners"
        :key="index"
        @click="toplayingpage(banner)"
      >
        <img :src="banner.image" />
        <div v-if="modes">
          <span v-for="(cate, index) in banner.cates" :key="index">{{cate.catename}}</span>
          <h4></h4>
          <h5>{{banner.wx_small_app_title}}</h5>
          <p>{{banner.app_fu_title}}</p>
        </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>
<script>
export default {
  name: "Homeswipe",
  props: ["banners", "modes"],
  methods: {
    toplayingpage(value) {
      // console.log('xxxx',value.postid);
      this.$store.state.showloading = true;
      let id
      if (value.extra_data) {
        id=value.extra_data.app_banner_param
      }else{
        id=value.postid

      }
      // this.$store.state.showplayingpage = true;
      this.$store.state.showrouter = false;

      this.playingpagedatas = JSON.parse(window.localStorage.getItem('playingpagedatas'))
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/view?postid=" +
            id
        )
        .then(response => {
          console.log(response);

          this.$store.commit("changeplayingpagedatas", response.data.data);
          this.$store.state.showloading = false;
          this.$store.state.showplayingpage = true;
        });
    }
  }
};
</script>
<style lang="less">
h3 {
  margin: 15px 0;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  position: relative;
  text-align: center;
  background-color: #39a9ed;
  &.modes {
    img {
      filter: brightness(0.7);
    }
  }
  img {
    width: 100%;
    vertical-align: middle;
    filter: brightness(1);
  }
  div {
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: left;
    letter-spacing: 1px;
    padding: 15px;
    span {
      font-size: 14px;
    }
    h4 {
      width: 50px;
      height: 1px;
      margin-top: 10px;
      background: lightcyan;
    }
    h5 {
      font-weight: 600;
      font-size: 16px;
      margin: 15px 0;
    }
    p {
      font-size: 14px;
    }
  }
}
</style>