<template>
  <div class="homelist">
    <h3 v-if="hshow">
      <slot></slot>
    </h3>
    <ul>
      <li
        :class="{modes:mode}"
        v-for="(list, index) in lists"
        :key="index"
        @click="goplayingpage(list)"
      >
        <img :src="list.image" />
        <div>
          <span>
            <a v-for="(cate, index) in list.cates" :key="index">{{cate.catename}}</a>
            / {{counttime(list.duration)}}
          </span>
          <p>{{list.title}}</p>
        </div>
      </li>
    </ul>
    <!-- <Playingpage v-if="showplayingpage" :datas="playingpagedatas"></Playingpage> -->
  </div>
</template>
<script>
// import Playingpage from "./Playingpage.vue";
export default {
  name: "Homelist",
  props: ["lists", "mode", "hshow"],
  data() {
    return {
      // playingpagedatas: null
    };
  },
  computed: {
    // showplayingpage() {
    //   return this.$store.state.showplayingpage;
    // },
    // playingpagedatas(){
    //   return this.$store.state.playingpagedatas;
    // }
  },
  // components: { Playingpage },
  methods: {
    goplayingpage(value) {
      // console.log("xxxxxxx",value.postid);
      this.$store.state.showloading = true;
      this.$store.state.showrouter = false;


      // this.$store.state.showplayingpage = true;
      // this.playingpagedatas = JSON.parse(window.localStorage.getItem('playingpagedatas'))
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/view?postid=" +
            value.postid
        )
        .then(response => {
          this.$store.commit("changeplayingpagedatas", response.data.data);
          this.$store.state.showplayingpage = true;
          this.$store.state.showloading = false;
        });
    },

    counttime(t) {
      return (
        parseInt(t / 60) + "'" + (t % 60 > 9 ? t % 60 : "0" + (t % 60)) + '"'
      );
    }
  }
};
</script>

<style lang="less" scoped>
.homelist {
  h3 {
    margin: 15px 0;
    text-align: center;
    font-weight: 600;
    font-size: 18px;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      position: relative;
      &.modes {
        width: 50%;
        &:first-child {
          width: 100%;
          p {
            margin: 14px 0;
            font-size: 14px;
          }
          span {
            font-size: 14px;
          }
        }
        span {
          font-size: 12px;
          font-weight: 200;
        }
        p {
          width: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;

          font-size: 12px;
          margin: 5px 0;
        }
      }
      img {
        width: 100%;
        filter: brightness(0.7);
        vertical-align: middle;
      }
      div {
        position: absolute;
        left: 0;
        bottom: 0;
        color: white;
        text-align: left;
        padding: 15px;
        letter-spacing: 1px;
        width: 90%;
        span {
          font-size: 14px;
          font-weight: 200;
        }
        p {
          font-weight: 600;
          margin: 14px 0;
          font-size: 14px;
        }
      }
    }
  }
}
</style>