<template>
  <div class="channellist" v-show="!showplayingpage">
    <p>
      <van-icon name="arrow-left" @click="hidechannellist" />
      <slot></slot>
      <van-icon name="arrow-left" class="kongbai" />
    </p>
    <Homelist :lists="lists.data" :mode="false"></Homelist>

    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="(loaddata, index) in loaddatas" :key="index">
        <Homelist :lists="loaddata" :mode="false"></Homelist>
      </van-cell>
    </van-list>
  </div>
</template>
<script>
import Homelist from "./Homelist.vue";
export default {
  name: "Channellist",
  props: ["lists", "loadid"],
  data() {
    return {
      loading: false,
      finished: false,
      pages: 2,
      loaddatas: []
    };
  },
  computed: {
    showplayingpage() {
      return this.$store.state.showplayingpage;
    },
  },
  components: {
    Homelist
  },
  methods: {
    hidechannellist() {
      this.$store.state.showchannellist = false;
    },
    onLoad() {
      this.pages += 1;

      this.axios
        .get(
          ` https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostInCate?p=${this.pages}&size=10&cateid=${this.loadid}`
        )
        .then(response => {
          // console.log(response.data.data);
          this.loaddatas.push(response.data.data);
          this.loading = false;
        });
    }
  }
};
</script>

<style lang="less" scoped>
.channellist {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;

  p {
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    font-weight: 600;
    .van-icon {
      font-size: 20px;
      font-weight: 800;
      &.kongbai {
        color: white;
      }
    }
  }
}
</style>
