<template>
  <div class="channel" v-show="showrouter">
    <ul>
      <li
        v-for="(channeldata, index) in channeldatas"
        :key="index"
        @click="gochannellist(channeldata,channeldata.catename)"
      >
        <img :src="channeldata.icon" />
        <p>
          <span>#{{channeldata.catename}}#</span>
        </p>
      </li>
    </ul>

    <Channellist
      v-if="showchannellist"
      :lists="channellistdatas"
      :loadid="channeldatas.cateid"
    >{{name}}</Channellist>
  </div>
</template>
<script>
import Channellist from "../components/Channellist.vue";
export default {
  name: "Channel",
  data() {
    return {
      channellistdatas: [],
      name: null
    };
  },
  computed: {
    showchannellist() {
      return this.$store.state.showchannellist;
    },
    channeldatas() {
      return this.$store.state.channeldatas;
    },

    showplayingpage() {
      return this.$store.state.showplayingpage;
    },
    showrouter() {
      return this.$store.state.showrouter;
    }
  },
  components: {
    Channellist
  },

  methods: {
    gochannellist(value, name) {
      // https://app.vmovier.com/apiv3/post/getPostInCate?p=1&size=10&cateid=8
      // let a = value.cateid
      this.$store.state.showchannellist = true;
      this.$store.state.showloading = true;
      this.$store.state.showrouter = false;

      this.name = name;
      let storagechannellistdata = JSON.parse(
        window.localStorage.getItem(`channellistdata${value.cateid}`)
      );

      if (
        storagechannellistdata &&
        storagechannellistdata.endtime > Date.now()
      ) {
        this.channellistdatas = storagechannellistdata.datas;
      } else {
        this.axios
          .get(
            "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostInCate?p=1&size=10&cateid=" +
              value.cateid
          )
          .then(response => {
            // console.log(response.data.data);
            this.channellistdatas = response.data;
            this.$store.state.showloading = false;
            this.$store.state.showrouter = true;

            window.localStorage.setItem(
              "channellistdata" + value.cateid,
              JSON.stringify({
                endtime: Date.now() + 1 * 60 * 60 * 1000,
                datas: response.data
              })
            );
          });
      }
    }
  }
};
</script>

<style lang="less"  scoped>
.channel {
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 50%;
      position: relative;
      img {
        width: 100%;
        vertical-align: middle;
        filter: brightness(0.7);
      }
      p {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        span {
          font-size: 18px;
          letter-spacing: 0.2em;
          font-weight: bold;
          color: cornsilk;
        }
      }
    }
  }
}
</style>